@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Poppins&display=swap');


* {
  margin: 0;
  padding: 0;
}

#container {
  width: 100%;
  background: var(--container-color);

}

.initial {
  display: flex;
  justify-content: space-between;
  height: 4rem;

  display: flex;
  align-items: center;
  padding: 1rem;
  background: var(--header-color);

  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;

  border-bottom: 1px solid var(--shadow-color);

}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15rem;
  animation: rec 0.8 steps(60);
}

.nav_button {
  display: flex;
  align-items: center;
  border: 0;
  background: rgba(255, 255, 255, 0);
}

.navigation {
  display: none;
}


.main {
  text-align: center;
}


.onest {
  margin: 4px var(--shadow-color);
  padding-top: 5rem;

  background: white;
}


.titulo {
  padding: 1.5rem;
  color: var(--title-color);
  font: 700 var(--font-size-fira) "Fira Sans", sans-serif;
}

.left {
  text-align: left;
  text-align: justify;
  padding: 1rem;
}

.left-aling {
  text-align: justify;
  font: 400 var(--font-size-poppins) "Poppins", italic;
  padding: 1rem;
  border-bottom: 0.16rem solid var(--shadow-color);
  color: var(--text-color);

}

.doctor1 {
  width: auto;
  height: 18rem;
  position: relative;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}


.imgbox::before {
  content: "";
  height: 18rem;
  width: 17rem;
  border: 2px solid var(--shadow-color);
  position: absolute;
  top: 10rem;
  left: 2rem;
  border-radius: 0.5rem;
  z-index: 0;
}


.secaccordion {
  display: none;
}




.options {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1rem 0;
}

.doctors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}




.parts_of_the_list {
  display: flex;
  flex-direction: row;
  padding: 1rem 1rem;
  text-align: center;
}

.sub_titulo {
  font: 400 1.32rem "Poppins", sans-serif;
  padding-bottom: 1rem;
}

.img_doctor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 2rem;
}

.name {
  border: 5px solid var(--shadow-color);
  border-radius: 2.48rem;
  height: 3rem;
  width: 3rem;
}

.doctor_writes {
  margin-left: 1rem;
  font: 400 var(--font-size-poppins) "Poppins", sans-serif;
  border-radius: 0.8rem;
  background: white;

}

.left {
  color: var(--text-color);
}

.lab {
  display: none;
}


.map iframe {
  height: 30rem;
  width: 100%;
  margin: 0;
  margin-top: 1rem;
  border: 0;

}

.map {
  height: 30rem;

  margin: 0;
}


.footer {

  height: 25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--footer-color);
  padding-top: 2rem;
  font: 400 "Poppins", sans-serif;
  color: var(--font-color);

}

.part_footer {
  padding-top: 1rem;
  padding-bottom: 0.25rem;
}

.link0 {
  color: var(--font-color);
  text-decoration: none;
}



/* //////////MEDIA/////////// */


@media (min-width: 1200px) {


  .nav:hover {
    animation: nav 0.5s steps(60);
  }

  .doctor1 {
    width: 24rem;
    height: 25rem;
  }

  .textaling {
    margin-right: 5rem;
  }

  .imgbox::before {
    content: "";
    height: 25rem;
    width: 25rem;
    border: 2px solid var(--shadow-color);
    position: absolute;
    top: 10rem;
    left: 0.5rem;
    border-radius: 0.5rem;
    z-index: 0;
  }

  .box_acc {
    display: flex;
    flex-direction: row;
    margin: 0;
  }

  .button_title {
    display: none;
  }



  .secaccordion {
    display: block;
  }

  .options {
    background: none;
  }

  .parts_of_the_list {
    width: 35rem;
  }
}


@media (min-width: 992px) {

  #container {
    min-width: 1225px;
  }

  header,
  main,
  footer {
    min-width: 1160px;
  }


  .navigation {
    display: flex;
  }

  .nav {
    display: flex;
    margin-right: 1rem;
    text-decoration: none;
    font: 400 var(--font-size-poppin-s) "Poppins", sans-serif;
    color: var(--title-color);
  }

  .titulo {
    font: 700 var(--font-size-fira-sans) "Fira Sans", sans-serif;
    display: flex;
    text-align: center;

  }

  .text {
    font: 400 var(--font-size-poppin-s) "Poppins", sans-serif;
    text-align: center;
  }

  .onest {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  .textaling {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .doctor1 {
    margin-left: 1rem;
  }

  .imgbox::before {
    margin-left: 1rem;

  }

  .doctor1,
  .desk .textaling {
    display: flex;
    justify-content: space-between;
  }

  .box_acc {
    display: flex;
    flex-direction: row;

    justify-content: space-between;
    width: 100%;
  }

  .desk {
    display: flex;
    flex-direction: column;
    width: 40rem;
    text-align: center;
  }


  .second {
    margin-bottom: 1rem;
    padding: 1.5rem;
    color: var(--title-color);
    font: 700 var(--font-size-fira) "Poppins", sans-serif;
  }

  .doctors {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .listen {
    width: 25rem;
  }


  .lab {
    display: flex;
    width: 35rem;
    height: auto;
    border-radius: 2rem;
    margin-right: 1rem;
  }

  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .big_logo {
    display: flex;
    align-items: center;

  }

  .part_footer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 5rem;
    margin-left: 5rem;
  }

  :root {
    --font-size-poppin-s: 1.25rem;
    --font-size-fira-sans: 2.25rem;
  }
}

@media (min-width: 1350px) {

  #container {
    display: flex;
    flex-direction: column;

  }

  .imgbox::before {
    display: none;
  }

  .doctor1 {
    padding: 1rem;
  }

  .initial {
    max-width: 100%;
    display: flex;
    align-items: center;

  }

  .onest,
  .options,
  .doctors {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
  }

  .doctors {
    width: auto;
    display: flex;
    justify-content: space-between;
  }

  .iframe {
    width: 100vw;
  }

  .onest {
    width: 100%;
  }
}


@keyframes nav {
  from {
    opacity: 0;

  }

  to {
    opacity: 1;
  }
}

@keyframes rec {
  0% {
    transform: translateX(80px);
  }

  100% {
    transform: translateX(0px);
  }
}

:root {
  --container-color: #f2f2f2;
  --header-color: #fff;
  --footer-color: #919071;
  --shadow-color: #919071;
  --title-color: #414022;
  --text-color: #414022;
  --font-color: #ffff;

  --font-size-poppins: 1.25rem;
  --font-size-fira: 2rem;

}