@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Poppins&display=swap');

.button {
  height: 5rem;
  width: 10rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: row;
  border-radius: 0.2rem;
  border: 0;
  background: var(--button-colort);
}


.link {
  text-decoration: none;
  color: var(--font-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font: 400 1.1rem "Poppins", sans-serif;

}

:root {
  --font-color: #ffff;
  --button-colort: #1c2244;
}