@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');

.accordion {
  width: 100%;

}

.accordion p {
  overflow: hidden;
  font: 400 1rem "Secular One", sans-serif;
  text-align: left;
  border-radius: 0.5rem;
  animation: go 1s;
  padding: 1rem;
  background: white;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-color);


}





.acc {
  margin: 2rem 2rem;

  height: 8rem;
  width: 8rem;
  border-radius: 2rem;
  border: 0;
  background: var(--button-colort);
}

.icons {
  height: 6rem;
  width: 6rem;
  animation: emerge 1s;

}

@media (min-width: 1200px) {
  .secaccordion {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .secaccordion:hover .after {
    display: none;
  }



  .secaccordion p {
    font: 400 1rem "Poppins", sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: emerge 1s;
    text-align: center;
    padding: 1.25rem;
    color: var(--text-color);


  }

  .accordion {
    display: none;
  }



  .secacc {
    width: 20rem;
    height: 20rem;
    margin: 1rem;
    border-radius: 0;
    border-radius: 2rem;
    transition: 0.5s;
    border: 1px solid var(--button-colort);


  }



  .secacc:hover {
    background: var(--button-colort);
    text-align: center;

  }

  .box_acc {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0;
  }



  .acc:hover {
    cursor: pointer;
    background: var(--button2-hover);
    transition: 0.6s;
  }


}


@media (min-width: 992px) {

  .accordiond .acc {
    margin: 0 1rem;
    display: grid;

  }

  .box_acc {
    display: flex;
    flex-direction: row;
  }

  .accordion p {
    width: 17rem;
    margin-left: 1.5rem;
    animation: go 1s;
  }



}



:root {
  --button-colort: #919071;
}

@keyframes go {
  0% {
    transform: translateY(2rem);
  }

  100% {
    transform: translateY(0);
  }

}

@keyframes emerge {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}